import s from './Searchbar.module.css';
import React, { useState } from 'react';
import { func } from 'prop-types';
import { toast } from 'react-toastify';
import { Form, Input } from 'semantic-ui-react';

function Searchbar({ getQuery, resetState, onSearchHandlerChange }) {
  const [query, setQuery] = useState('');

  const onSubmitButtonPressed = evt => {
    //   –≤–∏–º–∏–∫–∞—î –ø–µ—Ä–µ–∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω—è —Ñ–æ—Ä–º–∏ –ø–æ –∑–∞–º–æ–≤—á—É–≤–∞–Ω–Ω—ñ
    evt.preventDefault();

    // –ø–µ—Ä–µ–≤—ñ—Ä–∫–∞ —á–∏ –ø–æ–ª–µ –Ω–µ —î –ø—É—Å—Ç–∏–º
    if (query.trim() === '') {
      return toast.error('ü¶Ñ –≤–≤–µ–¥–∏ –Ω–∞–∑–≤—É —Ñ—ñ–ª—å–º—É —ñ –ø–æ–≤—Ç–æ—Ä–∏ –ø–æ—à—É–∫', {
        position: 'bottom-center',
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
    // —Å–∫–∏–¥–∞—î —Å—Ç–æ—Ä—ñ–Ω–∫—É –Ω–∞ "1" —ñ –∫–æ–ª–µ–∫—Ü—ñ—é –Ω–∞ "[]"
    resetState();

    // –ø–µ—Ä–µ–¥–∞—î –ø–æ—Ç–æ—á–Ω–∏–π –∑–∞–ø–∏—Ç
    getQuery(query);
    onSearchHandlerChange(query);
  };

  // —Å–ª—É—Ö–∞—î Input —Ç–∞ –æ–Ω–æ–≤–ª—é—î –∑–Ω–∞—á–µ–Ω–Ω—è query –°—Ç–µ–π—Ç—É
  const onInputHandleChange = evt => {
    setQuery(evt.target.value.toLowerCase());
  };

  return (
    <>
      <Form onSubmit={onSubmitButtonPressed} className={s.form}>
        <Input
          onChange={onInputHandleChange}
          type="text"
          placeholder="Search..."
          className={s.input}
        />
      </Form>
    </>
  );
}

Searchbar.propTypes = {
  getQuery: func.isRequired,
  resetState: func.isRequired,
};

export default Searchbar;
